class PicklistRemoteDataExampleComponent {
    constructor(dataService) {
        this.dataService = dataService;
        this.peopleLoading = true;
        this.peopleInput$ = new Subject();
        this.selectedPersons = this.dataService.mockPeople.slice(0, 2);
        this.remoteCount = this.dataService.mockPeople.length - this.selectedPersons.length;
        // using .bind() to maintain a scope that allows me access to the dataService in a callback function
        this.addCustomItemFn = this.addCustomItem.bind(this);
    }
    ngOnInit() {
        this.loadPeople();
    }
    trackByFn(item) {
        return item.id;
    }
    addCustomItem(term) {
        const customItem = {
            id: `1234-${term}`,
            age: Math.floor(Math.random() * 40) + 18,
            name: term
        };
        this.dataService.mockPeople.push(customItem);
        return customItem;
    }
    onChange() {
        this.remoteCount = this.dataService.mockPeople.length - this.selectedPersons.length;
        if (this.picklist.leftPaneIsEmpty && this.remoteCount > 0) {
            this.peopleLoading = true;
            this.loadPeople();
        }
    }
    loadPeople() {
        this.people$ = concat(of([]), // default items
        this.peopleInput$.pipe(distinctUntilChanged(), tap(() => this.peopleLoading = true), switchMap(term => this.dataService.getPeople(term, this.selectedPersons).pipe(catchError(() => of([])), // empty list on error
        tap(() => { this.peopleLoading = false; })))));
    }
}
